<template>
  <div id="app">
    <Nav />
    <div id="page1"></div>
    <img src="./assets/page1-bottom.jpg" alt="page1">
    <div id="page2" class="page"></div>
    <img src="./assets/page2-bottom.jpg" alt="page2">
    <div id="page3" class="page"></div>
    <img src="./assets/page3-bottom.jpg" alt="page3">
    <img src="./assets/user.jpg" alt="user">
    <img src="./assets/connect.jpg" alt="connect">
    <img src="./assets/bottom.jpg" alt="bottom">
  </div>
</template>

<script>
import $ from 'jquery'
import Particleground from 'Particleground.js';
import Nav from './Nav'

export default {
  name: 'app',
  components: {
    Nav
  },
  mounted() {
    new Particleground.particle('#page1')

    const nav = $('#nav')

    const page1 = $('#page1')
    const page1H = page1.height() - 75

    const page2 = $('#page2')
    const page3 = $('#page3')

    $(window).scroll(e => {
      const top = document.documentElement.scrollTop
      console.log(top)

      // nav
      if (top >= page1H) {
        nav.addClass('fixed')
      } else {
        nav.removeClass('fixed')
      }

      // page2
      changePosition(page2, 400)
      changePosition(page3, 1600)
    })
  }
}

function changePosition(el, offset) {
  const top = document.documentElement.scrollTop

  if (top >= offset) {
    el.css({
      backgroundPosition: `50% ${-1080 + top - offset}px`
    })
  } else {
    el.css({
      backgroundPosition: `50% -1080px`
    })
  }
}
</script>

<style lang="scss">
  @import url('./styles/reset.scss');
  body {
    overflow-x: hidden;
    height: 100vh;
  }

  #app {
    width: 100vw;
  }

  .page {
    background-position: 50% -1080px;
    background-repeat: no-repeat;
    width: 100vw;
    height: 800px;
  }

  #page1 {
    position: relative;
    background: url(./assets/page1-content.jpg) no-repeat;
    background-size: 100% 100%;
    width: 100vw;
    height: 100vh;
  }

  #page2 {
    background-image: url(./assets/page2-content.jpg);
  }

  #page3 {
    background-image: url(./assets/page3-content.jpg);
  }
</style>



